@keyframes flash {
  0% {
    background-color: grey;
    opacity: 0.6;
    border: 1px solid whitesmoke;
    box-shadow: 1px 1px 20px whitesmoke, -1px -1px 20px whitesmoke;
  }
  25% {
    background-color: grey;
    opacity: 0.3;
    border: 1px solid whitesmoke;
    box-shadow: 3px 3px 15px whitesmoke, -3px -3px 15px whitesmoke;
  }
  50% {
    background-color: grey;
    opacity: 0.3;
    border: 1px solid whitesmoke;
    box-shadow: 5px 5px 10px whitesmoke, -5px -5px 10px whitesmoke;
  }
  100% {
    background-color: grey;
    opacity: 0.6;
    border: 1px solid whitesmoke;
    box-shadow: 7px 7px 5px whitesmoke, -7px -7px 5px whitesmoke;
  }
}

#drum-machine {
  display: flex;
  justify-content: center;
  background-color: #f7d7fa;
  padding-top: 30vh;
  height: 100vh;
}
#display {
  display: flex;
  
}
#sidebar {
  height: 4em;
  width: 4em;
  text-align: center;
  align-items: center;
  justify-content: center;
  border: 2px transparent;
  margin-left: 1em;
  animation: flash 1s linear -0.5s infinite alternate;

}
#sidebar:focus {
  animation: flash 1s ease-linear -0.5s infinite alternate;
}
#sidebar:active {
animation: none;}

#drumpad {
  border: 1px solid black;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr 1fr;
  background-color: #aca1ad;
  height: 45vh;
  width: 45vh;
  text-align: center;
}
.drum-pad {
  border: 1px solid black;
  display: flex;
  justify-content: center;
  align-items: center;
}